@media (min-width: 1280px){

	section.about_page{
		padding-bottom: 48px !important;
	}
}

@media (min-width: 1540px){

	section.about_page{
		width: 1380px;
		margin: 0 auto;
	}
}

section.about_page .foreward{
	margin-bottom: 40px;
}

@media (min-width: 1024px){

	section.about_page .foreward{
		margin-bottom: 64px;
		margin-top: 40px;
	}
}

@media (min-width: 1280px){

	section.about_page .foreward{
		width: 750px;
	}
}

section.about_page .foreward p{
	font-size: 18px;
	line-height: 28px;
	font-weight: bold;
	color: #747174;
	letter-spacing: .5px;
}

@media (min-width: 768px){

	section.about_page .foreward p{
		font-size: 20px;
		line-height: 30px;
	}
}

@media (min-width: 1024px){

	section.about_page .about_content{
		position: relative;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-pack: justify;
		    -ms-flex-pack: justify;
		        justify-content: space-between;
		position: relative;
	}
}

@media (min-width: 1280px){

	section.about_page .about_content{
		-webkit-box-pack: unset;
		    -ms-flex-pack: unset;
		        justify-content: unset;
