}

@media (min-width: 1024px){

	section.about_page .about_content .illustraion{
		width: 400px;
	}
}

@media (min-width: 1280px){

	section.about_page .about_content .illustraion{
		width: 500px;
		margin-right: 80px;
		position: relative;
	}
}

@media (min-width: 1540px){

	section.about_page .about_content .illustraion{
		width: 550px;
		margin-right: 120px;
	}
}

section.about_page .about_content .illustraion .why, section.about_page .about_content .illustraion .what{
	margin-bottom: 40px;
}

@media (min-width: 1024px){

	section.about_page .about_content .illustraion .why, section.about_page .about_content .illustraion .what{
		margin-bottom: 64px;
	}
}

section.about_page .about_content .illustraion .why h1,section.about_page .about_content .illustraion .what h1{
	font-size: 18px;
	line-height: 28px;
	color: #747174;
	letter-spacing: .5px;
	margin: 0;
	margin-bottom: 8px;
}

@media (min-width: 768px){

	section.about_page .about_content .illustraion .why h1,section.about_page .about_content .illustraion .what h1{
		font-size: 20px;
		line-height: 30px;
		margin-bottom: 16px;
	}
}

section.about_page .about_content .illustraion .why p,section.about_page .about_content .illustraion .what p{
	font-size: 14px;
	line-height: 21px;
	color: #747174;
	letter-spacing: .5px;
	margin-bottom: 8px;
}

@media (min-width: 768px){

	section.about_page .about_content .illustraion .why p,section.about_page .about_content .illustraion .what p{
		font-size: 16px;
		line-height: 26px;
	}
}

section.about_page .about_content .illustraion .contact{
	display: none;
}

@media (min-width: 1024px){

	section.about_page .about_content .illustraion .contact{
		display: block;
	}
}

@media (min-width: 1280px){

	section.about_page .about_content .illustraion .contact{
		position: absolute;
		bottom: 0;
		left: 0;
	}
}

section.about_page .about_content .pic{
	width: 100%;
	padding-top: 133.33333%;
	position: relative;
	margin-bottom: 24px;
}

@media (min-width: 1024px){

	section.about_page .about_content .pic{
		width: calc(100% - 480px);
		padding-top: calc(133.33333% - 640px);
		margin: 0;
	}
}

@media (min-width: 1280px){

	section.about_page .about_content .pic{
		width: calc(100% - 620px);
		padding-top: calc(133.33333% - 826.66667px);
	}
}

@media (min-width: 1540px){

	section.about_page .about_content .pic{
		width: calc(100% - 710px);
		padding-top: calc(133.33333% - 946.66667px);
	}
}

section.about_page .about_content .pic img{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
}

section.about_page .about_content .contact span{
	font-size: 14px;
	line-height: 21px;
	display: block;
	letter-spacing: .5px;
	color: #747174;
	font-weight: bold;
	font-family: 'Open sans', serif;
}

section.about_page .scrolldown{
	display: none;
}

@media (min-width: 1280px){

	section.about_page .scrolldown{
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		    -ms-flex-direction: column;
		        flex-direction: column;
		position: fixed;
		right: 80px;
		bottom: 40px;
		cursor: pointer;
		overflow: hidden;
		height: 128px;
	}

	section.about_page .scrolldown span{
		font-family: 'Open sans', serif;
		-webkit-writing-mode: vertical-rl;
		    -ms-writing-mode: tb-rl;
		        writing-mode: vertical-rl;
		font-size: 14px;
		font-weight: bold;
		color: #747174;
		letter-spacing: .5px;
	}

	section.about_page .scrolldown .effectLine{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		-webkit-transform: translateY(150%);
		        transform: translateY(150%);
		background-color: #747174;
		text-align: right;
	}

	section.about_page .scrolldown .arrow:first-child{
		-webkit-transform: rotate(90deg) scale(.5);
		        transform: rotate(90deg) scale(.5);
	}

	section.about_page .scrolldown .arrow{
		-webkit-transform: rotate(-90deg) scale(.5);
		        transform: rotate(-90deg) scale(.5);
	}
}

.switchAnimation{
	-webkit-animation: switchScrollDownText 1s;
	        animation: switchScrollDownText 1s;
}

.dn{
	display: none;
}

@-webkit-keyframes switchScrollDownText{
	0%{
		-webkit-transform: translateY(150%);
		        transform: translateY(150%);
	}
	60%{
		-webkit-transform: translateY(0%);
		        transform: translateY(0%);
	}
	100%{
		-webkit-transform: translateY(-150%);
		        transform: translateY(-150%);
	}
}

@keyframes switchScrollDownText{
	0%{
		-webkit-transform: translateY(150%);
		        transform: translateY(150%);
	}
	60%{
		-webkit-transform: translateY(0%);
		        transform: translateY(0%);
	}
	100%{
		-webkit-transform: translateY(-150%);
		        transform: translateY(-150%);
	}
}

