.verse_items{
	cursor: pointer;
	display: table;
	position: relative
}

.verse_items h2{
	font-size: 16px;
	line-height: 24px;
	color: #747174;
	margin: 0;
	margin-bottom: 16px;
	cursor: pointer;
	-webkit-transition: .5s;
	transition: .5s;
	position: relative;
	z-index: 10
}

.verse_items h2:before{
	content: '';
	height: 100%;
	width: 0;
	position: absolute;
	left: -5px;
	top: 0;
	background-color: #747174;
	-webkit-transition: .5s;
	transition: .5s;
	z-index: -1;
}

@media (min-width: 768px){

	.verse_items h2{
		font-size: 20px;
		line-height: 30px;
		letter-spacing: 1px;
		margin-bottom: 24px
	}
}

.desktop .verse_items h2:hover{
	color: #fff;
	-webkit-transition: .5s;
	transition: .5s
}

.desktop .verse_items h2:hover:before{
	width: calc(100% + 10px);
	-webkit-transition: .5s;
	transition: .5s;
}


