.verselist .interaction{
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1000;
    background-repeat: repeat;
}
@media (min-width: 1280px){
    .verselist .interaction{
        display: none;
    }
}
.verselist .interaction .container{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.verselist .interaction .container .main-visual{
    width: calc(100vw - 80px);
    margin-bottom: 80px;
}
@media (min-width: 768px){
    .verselist .interaction .container .main-visual{
        width: calc(100vw - 160px);
    }
}
.verselist .interaction .container .main-visual svg path{
    fill: white;
}
.verselist .interaction .container .interaction-nav{
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -ms-align-items: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-bottom: 24px;
}
.verselist .interaction .container .interaction-nav .drag-square{
    width: 40px;
    height: 40px;
    border: 1px solid white;
    position: relative;
    padding: 13px 15px;
    -webkit-transition: .2s;
    transition: .2s;
}
.verselist .interaction .container .interaction-nav .drag-square .arrow{
    width: 10px;
    -webkit-transform-origin: center;
            transform-origin: center;
}
.verselist .interaction .container .interaction-nav .drag-square .arrow svg{
    -webkit-transform: scale(-1,1);
            transform: scale(-1,1);
    -webkit-transform-origin: center;
            transform-origin: center;
}
.verselist .interaction .container .interaction-nav .drag-square .arrow svg path{
    stroke: #fff;
}
.verselist .interaction .container .interaction-nav .connecting-line{
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 140px;
    margin: 0 10px;
}
.verselist .interaction .container .interaction-nav .connecting-line span{
    margin: 0 2px;
    width: 5px;
    height: 1px;
    background-color: #747174;
}
.verselist .interaction .container .interaction-nav .target-square{
    border: 1px solid #747174;
    width: 40px;
    height: 40px;
}
.verselist .interaction .container .illustration{
    text-align: center;
}
.verselist .interaction .container .illustration span{
    color: #747174;
    font-size: 16px;
    letter-spacing: 1px;
}
.verselist .listBg{
    width: 100vw;
    height: 100vh;
    opacity: .5;
    background-size: cover;
    background-position: center 30%;
    position: fixed;
    top: 0;
    left: 0;
    -webkit-transition: .8s;
    transition: .8s;
    opacity: 0;
    display: none;
}
@media (min-width: 1280px){
    .verselist .listBg{
        display: block;
    }
}
.verselist .verselist{
    display: none;
}
@media (min-width: 1280px){
    .verselist .verselist{
        display: block;
    }
}
.verselist .interaction.hidden{
    display: none;
}
.verselist .listBg.visible{
    display: block;
}
.verselist .verselist.visible{
    display: block;
}


